body {
  margin: 0;
  padding: 0;
  font-family: "Heebo", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.HomePage {
  overflow: hidden;
  display: grid;
  grid-template-columns: 100%;
  grid-template-areas:
    "header"
    "carouselOne"
    "carouselTwo"
    "carouselThree";

  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 0px;
  margin: 0px;
}

#HomePageHeader {
  grid-area: header;
}

#HomePageCarouselOne {
  grid-area: carouselOne;
  padding: 0px;
  margin: 0px;
}

#HomePageCarouselTwo {
  grid-area: carouselTwo;
  padding: 0px;
  margin: 0px;
}

#HomePageCarouselThree {
